import { useRef, useEffect, useState } from "react";
import clsx from 'clsx';
import CircularProgress from '@material-ui/core/CircularProgress';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { TextField, Button } from "@material-ui/core";
import Search from '@material-ui/icons/Search';
import load, { Response, Params } from '../../services/dashboard';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  number: {
      marginTop: 30
  }
}));

const Main = () => {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    const dtiRef = useRef<HTMLInputElement>(null);
    const dtfRef = useRef<HTMLInputElement>(null);
    const [isLoading, setLoading] = useState(true);
    const [pageData, setPageData] = useState<Response | null>();

    const today:Date = new Date();
    const _dtf:string = getDate(today);
    today.setDate(today.getDate() - 10);
    const _dti:string = getDate(today);

    useEffect(()=>{
      loadPage({ dti: _dti, dtf:_dtf});
    },[]);

    function getDate(date:Date) {
      const month:string = `${(date.getMonth()+1) > 9 ? '' : '0'}${date.getMonth()+1}`;
      const day:string = `${(date.getDate()+1) > 9 ? '' : '0'}${date.getDate()+1}`;
      const year:string = date.getFullYear().toString();
      return `${year}-${month}-${day}`;
    }

    async function loadPage(query:Params){
      const data = await load(query);
      setLoading(false);
      if(data){
        setPageData(data);
      } else {
      }
    }

    function submitHandler(){
      const dti:string|undefined = dtiRef.current?.value;
      const dtf:string|undefined = dtfRef.current?.value;

      if (dti && dtf) {
        setLoading(true);
        setPageData(null);
        loadPage({ dti, dtf });
      } else {
        alert('Please, select start and end date.');
      }
    }

    function getPage() {
      return <Grid container spacing={3}>
        <Grid item  xs={6} md={3} lg={3}>
            <TextField defaultValue={_dti} fullWidth variant="outlined" focused label="Starts in"  type="date" inputRef={dtiRef} />
        </Grid>
        <Grid item  xs={6} md={3} lg={3}>
            <TextField defaultValue={_dtf} fullWidth variant="outlined" focused label="Ends in"  type="date" inputRef={dtfRef} />
        </Grid>
        <Grid item  xs={12} md={3} lg={3}>
            <Button type="submit" variant="contained" color="primary" onClick={submitHandler}>
                <Search />
            </Button>
        </Grid>
        
        <Grid item xs={12} md={4} lg={4}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Helpers</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.helpers}</Typography>
                </div>
            </Paper>
        </Grid>
        
        <Grid item xs={12} md={4} lg={4}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Companies</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.companies}</Typography>
                </div>
            </Paper>
        </Grid>
        <Grid item xs={12} md={4} lg={4}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Jobs</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.jobs}</Typography>
                </div>
            </Paper>
        </Grid>

        <Grid item xs={12} md={3} lg={3}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Invites</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.jobs_invited}</Typography>
                </div>
            </Paper>
        </Grid>
        
        <Grid item xs={12} md={3} lg={3}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Waiting</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.jobs_waiting}</Typography>
                </div>
            </Paper>
        </Grid>
        <Grid item xs={12} md={3} lg={3}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Confirmed</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.jobs_confirmed}</Typography>
                </div>
            </Paper>
        </Grid>
        <Grid item xs={12} md={3} lg={3}>
            <Paper className={fixedHeightPaper}>
                <Typography variant="h6" noWrap>Rejected</Typography>
                <div className={classes.number}>
                    <Typography align='right' variant="h1">{pageData?.jobs_rejected}</Typography>
                </div>
            </Paper>
        </Grid>
      </Grid>;
    }

    return(
        <Container maxWidth="lg" className={classes.container}>
            {(!isLoading && pageData) ? getPage() : null}
            {isLoading ? <CircularProgress /> : null}
        </Container>
    );
}

export default Main;