import { useEffect, useState } from "react";
import CircularProgress from '@material-ui/core/CircularProgress';
import Divider from '@material-ui/core/Divider';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useAuth } from "../../contexts/auth";
import {
  useParams
} from "react-router-dom";
import { read} from '../../services/jobs';

interface FormProps {}

interface JobURL{
  id?:string | null | undefined;
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    box: {
      paddingTop: 20,
      paddingBottom: 20
    },
  }),
);

export const Form: React.FunctionComponent<FormProps> = (props:any) => {
  const company:JobURL = useParams();
  const [isLoading, setLoading] = useState(false);
  const [jobData, setCompanyData] = useState<any>(undefined);
  const {api} = useAuth();
  const classes = useStyles();


  useEffect(()=> {
    async function checkUser(){
      if(Object(company).hasOwnProperty('id')){
        const id = company.id;
        if(id){
          setLoading(true);
          const response = await read({id, api});

          if(response && response.data){
            setCompanyData(response.data);
          }
      
          setLoading(false);
        }
      }
    }
    
    checkUser();
  },[api, company]);

  function getPage(){

    if(!jobData) return null;

    return <>
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Company
        </Typography>
        {jobData.company.name}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Address
        </Typography>
        {`${jobData.state}, ${jobData.city}, ${jobData.street} - ${jobData.zipcode}`}
        </div>
      <Divider />
      {
        jobData.labor ? <>
          <div className={classes.box}>
            <Typography variant="h5" gutterBottom>
              Labor
            </Typography>
           Yes
          </div>
          <Divider />
        </> : null
      }
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Date post
        </Typography>
        {jobData.date_post.formated}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Date/Period
        </Typography>
        {jobData.date_start.formated} - {jobData.date_end.formated}
      </div>
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Time start/end
        </Typography>
        {jobData.time_start} - {jobData.time_end}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Expires in
        </Typography>
        {jobData.expires_in.formated}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Number of positions
        </Typography>
        {jobData.number_positions}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Positions filled
        </Typography>
        {jobData.positions_filled}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Rate/Rate max.
        </Typography>
        {jobData.rate}/{jobData.rate_max}
      </div>
      <Divider />
      
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Subskills
        </Typography>
        {
          jobData.subskills ? jobData.subskills.map((item:{name:string, id:number}) => {
            return <div>{item.name}</div>
          }) : null
        }
      </div>
    </>;
  }

  return (
    <>
      {!isLoading ? getPage() : null}
      {isLoading ? <CircularProgress /> : null}
    </>
  );
};

export default Form;