import { useEffect, useState } from "react";
import CircularProgress from '@material-ui/core/CircularProgress';
import Divider from '@material-ui/core/Divider';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Shifts from "./shifts";
import { useAuth } from "../../contexts/auth";
import {
  useParams
} from "react-router-dom";
import { read} from '../../services/app-users';

interface FormProps {}

interface AppUserURL{
  id?:string | null | undefined;
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    box: {
      paddingTop: 20,
      paddingBottom: 20
    }
  }),
);

export const Form: React.FunctionComponent<FormProps> = (props:any) => {
  const company:AppUserURL = useParams();
  const [isLoading, setLoading] = useState(false);
  const [userData, setUserData] = useState<any>(undefined);
  const {api} = useAuth();
  const classes = useStyles();


  useEffect(()=> {
    async function checkUser(){
      if(Object(company).hasOwnProperty('id')){
        const id = company.id;
        if(id){
          setLoading(true);
          const response = await read({id, api});

          if(response && response.data){
            setUserData(response.data);
          }
      
          setLoading(false);
        }
      }
    }
    
    checkUser();
  },[api, company]);

  function getPage(){

    if(!userData) return null;

    return <>
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          User
        </Typography>
        {userData.name} {`${userData.phone_number ? ' - ' + userData.phone_number: ''}`}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Address
        </Typography>
        {`${userData.user_data.state}, ${userData.user_data.city}, ${userData.user_data.street} - ${userData.user_data.zipcode}`}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Driver License
        </Typography>
        {`${userData.user_data.driver_license ? 'Yes' : 'No'}`}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Labor
        </Typography>
        {`${userData.user_data.labor ? 'Yes' : 'No'}`}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Own car
        </Typography>
        {`${userData.user_data.own_car ? 'Yes' : 'No'}`}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Availability
        </Typography>
        <Shifts
          title='Monday'
          shift1={userData.user_data.mo_1}
          shift2={userData.user_data.mo_2}
          shift3={userData.user_data.mo_3}
        />
        <Shifts
          title='Tuesday'
          shift1={userData.user_data.tu_1}
          shift2={userData.user_data.tu_2}
          shift3={userData.user_data.tu_3}
        />
        <Shifts
          title='Wednesday'
          shift1={userData.user_data.we_1}
          shift2={userData.user_data.we_2}
          shift3={userData.user_data.we_3}
        />
        <Shifts
          title='Thursday'
          shift1={userData.user_data.th_1}
          shift2={userData.user_data.th_2}
          shift3={userData.user_data.th_3}
        />
        <Shifts
          title='Friday'
          shift1={userData.user_data.fr_1}
          shift2={userData.user_data.fr_2}
          shift3={userData.user_data.fr_3}
        />
        <Shifts
          title='Saturday'
          shift1={userData.user_data.sa_1}
          shift2={userData.user_data.sa_2}
          shift3={userData.user_data.sa_3}
        />
        <Shifts
          title='Sunday'
          shift1={userData.user_data.su_1}
          shift2={userData.user_data.su_2}
          shift3={userData.user_data.su_3}
        />
      </div>
      <Divider />
      {
        userData.subskills.length ? <div className={classes.box}>
          <Typography variant="h5" gutterBottom>
            Subskills
          </Typography>
          {
            userData.subskills.length ? userData.subskills.map((item:{name:string, id:number}) => {
              return <div>{item.name}</div>
            }) : null
          }
        </div> : null
      }
      
    </>;
  }

  return (
    <>
      {!isLoading ? getPage() : null}
      {isLoading ? <CircularProgress /> : null}
    </>
  );
};

export default Form;