import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    box: {
      paddingTop: 20,
      paddingBottom: 20
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary
    },
    active: {
        background: '#23DC7A',
        fontWeight: 'bold'
    }
  }),
);

type Props = {
    title:string;
    shift1:null|boolean;
    shift2:null|boolean;
    shift3:null|boolean;
}

function Shifts(props:Props){
    const classes = useStyles();

    return <Grid container spacing={3}>
    <Grid item xs={3}>
      {props.title}
    </Grid>
    <Grid item xs={3}>
      <Paper className={`${classes.paper} ${props.shift1 ? classes.active : ''}`}>1º Shift</Paper>
    </Grid>
    <Grid item xs={3}>
      <Paper className={`${classes.paper} ${props.shift2 ? classes.active : ''}`}>2º Shift</Paper>
    </Grid>
    <Grid item xs={3}>
      <Paper className={`${classes.paper} ${props.shift3 ? classes.active : ''}`}>3º Shift</Paper>
    </Grid>
  </Grid>;

}

export default Shifts;