import api, { URL } from '../services/api';

export type SignInParams = {
    user:string,
    password:string
}

export interface AuthResponse {
    token: string
}

export function signIn<SignInParams>(props:SignInParams):Promise<AuthResponse | null>{
    return new Promise( resolve => {
        api.post(URL.login, props)
          .then(function (response) {
            resolve(response.data);
          })
          .catch(function (error) {
            if( error.response && error.response.data && error.response.data.error ){
               alert(error.response.data.error);
            } else {
                alert('Something wrong is going on, please try again later.');
            }
            resolve(null);
          });
        // setTimeout(() => {
        //     resolve({
        //         token: 'abc',
        //         user: {
        //             name: 'jhonnatan',
        //             email: 'jhonnatan@jobfishing.com'
        //         }
        //     });
        // },3000);
    });
}


export function loadByToken():Promise<AuthResponse | null>{
  return new Promise( resolve => {
      api.get(URL.loadByToken)
        .then(function (response) {
          resolve(response.data);
        })
        .catch(function (error) {
          if( error.response && error.response.data && error.response.data.error ){
             alert(error.response.data.error);
          } else {
          }
          resolve(null);
        });
  });
}