import api, { URL } from './api';

export type Params = {
    dti:string;
    dtf:string;
}

export interface Response {
  helpers: number;
  companies:number;
  jobs:number;
  jobs_waiting:number;
  jobs_invited:number;
  jobs_confirmed:number;
  jobs_rejected:number;
}

export default function load(props:Params):Promise<Response | null>{
    return new Promise( resolve => {
      const _url:string = `${URL.dashboard.page}?dti=${props.dti}&dtf=${props.dtf}`;
      api.get(_url)
          .then(function (response) {
            resolve(response.data);
          })
          .catch(function (error) {
            if( error.response && error.response.data && error.response.data.error ){
               alert(error.response.data.error);
            } else {
                alert('Something wrong is going on, please try again later.');
            }
            resolve(null);
          });
    });
}