import { URL } from './api';
import IPagination from '../interfaces/IPagination';

export type Params = {
  name: string;
  helper: null|number;
  id: number;
}

export interface ResponseList {
  items: Params[];
  pagination: IPagination;
};

type ListParams = {
  page:number;
  limit:number;
  api:any;
  search:string;
};

export default function list(props:ListParams):Promise<ResponseList | null>{
    const api:any = props.api;
    return new Promise( resolve => {
      const _url:string = `${URL.appUsers.page}?page=${props.page}&limit=${props.limit}&${props.search}`;
      api.get(_url)
          .then(function (response:any) {
            resolve(response.data);
          })
          .catch(function (error:any) {
            if( error.response && error.response.data && error.response.data.error ){
               alert(error.response.data.error);
            } else {
                alert('Something wrong is going on, please try again later.');
            }
            resolve(null);
          });
    });
}

export type ResponseCompany = {
  data: any;
  status: boolean;
}

export type ReadParams = {
  id:string|number;
  api:any;
}

export function read(props:ReadParams):Promise<ResponseCompany | null>{
  const api:any = props.api;
  return new Promise( resolve => {
    const _url:string = `${URL.appUsers.page}${props.id}`;
    api.get(_url)
        .then(function (response:any) {
          resolve(response.data);
        })
        .catch(function (error:any) {
          if( error.response && error.response.data && error.response.data.error ){
             alert(error.response.data.error);
          } else {
              alert('Something wrong is going on, please try again later.');
          }
          resolve(null);
        });
  });
}
