import { useEffect, useState, FormEvent } from "react";
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import { Button, TextField } from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import Search from '@material-ui/icons/Search';
import TablePagination from '@material-ui/core/TablePagination';
import CircularProgress from '@material-ui/core/CircularProgress';
import InputLabel from '@material-ui/core/InputLabel';
import {
    Link,
    useRouteMatch
} from "react-router-dom";
import { useAuth } from "../../contexts/auth";
import { ResponseList } from '../../services/jobs';
import HelpersList from './list-load';

import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import convertDbDate from "../../utils/convertDbDate";

import skillsList from '../../services/skills';
import subskillsList from '../../services/subskills';

interface FilterParams{
    name: string;
    state: string;
    city: string;
    zipcode: string;
    starts_in:Date | string;
    ends_in:Date | string;
    category:number|string;
    subskill:number|string;
}

const INITIAL_DATA:FilterParams = {
    name: '',
    state: '',
    city: '',
    zipcode: '',
    starts_in: '',
    ends_in: '',
    category: '',
    subskill: ''
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 140,
    },
    button: {
        margin: theme.spacing(1),
    }
  }),
);

interface ISkill {
    english:string;
    id:string|number;
}

const List = () => {
    const classes = useStyles();
    let { url } = useRouteMatch();
    const [isLoading, setLoading] = useState(true);
    const [pageData, setPageData] = useState<ResponseList | null>();
    const [page, setPage] = useState(0);
    const [limit, setlimit] = useState(10);
    const [searchString, setSearchString] = useState<string>('');
    const [filterData, setFilterData] = useState<FilterParams>(INITIAL_DATA);
    const [skills, setSkills] = useState<ISkill[]|undefined>(undefined);
    const [subskills, setSubskills] = useState<ISkill[]|undefined>(undefined); 
    const {api} = useAuth();

    useEffect(()=>{
        async function load(){
            const _skills = await skillsList({api});
            if (_skills) {
                setSkills(_skills.items);
            }

            const _subskills = await subskillsList({api});
            if (_subskills) {
                setSubskills(_subskills.items);
            }

            await HelpersList(page, limit, setLoading, setPageData, api, searchString);

        }
        load();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);


    const handleChangePage = async (event:any, newPage:any) => {
        setPage(newPage);
        await HelpersList(newPage, limit, setLoading, setPageData, api, searchString);
    };

    const handleChangeRowsPerPage = async (event:any) => {
        const _page:number = 1;
        const _limit:number = parseInt(event.target.value);
        setlimit(_limit);
        setPage(_page);
        await HelpersList(_page, _limit, setLoading, setPageData, api, searchString);
    };

    const filterChange = (event:any) => {
        const {name, value} = event.target;
        setFilterData({...filterData, ...{[name]: value}});
        console.log(name, value)
    };

    const onSubmit = async (event: FormEvent) => {
        event.preventDefault();
        const _f:any = filterData;
        let _s:any = new URLSearchParams(_f).toString();
        setSearchString(_s);
        setPageData(null);
        await HelpersList(page, limit, setLoading, setPageData, api, _s);
    }

    const skillsHandler = (event: React.ChangeEvent<{ value: unknown }>) => {
        const {value} = event.target;
        if (value){
            setFilterData({...filterData, ...{category: value as number }});
        }
    };

    const subskillsHandler = (event: React.ChangeEvent<{ value: unknown }>) => {
        const {value} = event.target;
        if (value){
            setFilterData({...filterData, ...{subskill: value as number }});
        }
    };

    function getDate(dti:string, dtf:string) {
        let ds:string = dti as string;
        let de:string = dtf as string;

        return  `${convertDbDate(ds)} - ${convertDbDate(de)}`;
    }

    function clearFilter(){
        setFilterData(INITIAL_DATA);
    }

    function getPage(){
        return <>
                <form onSubmit={onSubmit}>
                    <Grid container spacing={3} style={{marginBottom: 30}}>
                        <Grid item  xs={6} md={2} lg={2}>
                            <FormControl  className={classes.formControl}>
                            <InputLabel>Category</InputLabel>
                                <Select
                                value={filterData.category}
                                onChange={skillsHandler}
                                >
                                    {
                                    skills && skills.map( (item:ISkill ) => <MenuItem value={item.id}>{item.english}</MenuItem>)
                                    }
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item  xs={6} md={3} lg={3}>
                            <FormControl  className={classes.formControl}>
                            <InputLabel >Service</InputLabel>
                                <Select
                                value={filterData.subskill}
                                onChange={subskillsHandler}
                                >
                                    {
                                    subskills && subskills.map( (item:ISkill ) => <MenuItem value={item.id}>{item.english}</MenuItem>)
                                    }
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item lg={2}>
                            <TextField value={filterData.state} onChange={filterChange}  name='state' fullWidth focused variant="outlined" label="State" />
                        </Grid>
                        <Grid item lg={2}>
                            <TextField value={filterData.city} onChange={filterChange}  name='city' fullWidth focused variant="outlined" label="City" />
                        </Grid>
                        <Grid item lg={2}>
                            <TextField value={filterData.zipcode} onChange={filterChange}  name='zipcode' fullWidth focused variant="outlined" label="Zipcode" />
                        </Grid>
                        {/* <Grid item  xs={6} md={3} lg={2}>
                            <TextField value={filterData.starts_in} onChange={filterChange}  name='starts_in' fullWidth variant="outlined" focused label="Starts in" type="date" />
                        </Grid>
                        <Grid item  xs={6} md={3} lg={2}>
                            <TextField value={filterData.ends_in} onChange={filterChange}  name='ends_in' fullWidth variant="outlined" focused label="Ends in" type="date" />
                        </Grid> */}
                        <Grid item  xs={12} md={3} lg={3}>
                            <Button className={classes.button} type="submit" variant="contained" color="primary" size="large">
                                <Search />
                            </Button>
                            <Button type='button' onClick={clearFilter} className={classes.button} color="secondary">Clear filter</Button>
                        </Grid>
                    </Grid>
                </form>
                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell>Company</TableCell>
                            <TableCell>Subskills</TableCell>
                            <TableCell>Labor</TableCell>
                            <TableCell>Date</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                        {pageData ? pageData?.items.map((row) => (
                            <TableRow key={`job__${row.id}`}>
                                <TableCell component="th" scope="row">
                                    <Link to={`${url}/${row.id}`}>
                                    {row.company.name}
                                    </Link>
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    <Link to={`${url}/${row.id}`}>
                                    {row.subskills}
                                    </Link>
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    <Link to={`${url}/${row.id}`}>
                                    {row.labor ? 'Yes' : 'No'}
                                    </Link>
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    <Link to={`${url}/${row.id}`}>
                                    {getDate(row.date_start, row.date_end)}
                                    </Link>
                                </TableCell>
                            </TableRow>
                        )) : null}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    component="div"
                    count={pageData ? (pageData.pagination.total || 0) : 0}
                    page={page}
                    onChangePage={handleChangePage}
                    rowsPerPage={limit}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </>;
    }
    return(
        <>
            {(!isLoading && pageData) ? getPage() : null}
            {isLoading ? <CircularProgress /> : null}
        </>
    );
}

export default List;