import React, {FormEvent, useEffect, useState} from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import marca from '../assets/images/marca.png';
import {useAuth} from '../contexts/auth';
import { SignInParams } from '../services/auth';

const useStyles = makeStyles((theme) => ({
    avatar: {
      margin: '0 auto',
      display: 'block'
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
      textAlign: 'center'
    },
}));

export default function Signin() {
  const classes = useStyles();
  const { signIn } = useAuth();
  const [data, setData] = useState({} as SignInParams);

  function onSubmit (event: FormEvent){
    event.preventDefault();
    if (data.user && data.password) {
      signIn(data);
    } else {
      alert('User and password are required');
    }
  };

  function onChangeHandler(e:any) {
    const { name, value } = e.currentTarget;
    const newData:SignInParams = {...data, ...{[name]: value}};
    setData(newData);
  }

  return (
    <React.Fragment>
      <CssBaseline />
      <Container component="main" maxWidth="xs"  style={{marginTop: 100}}>
      <img src={marca} alt=""  className={classes.avatar} />
        
        <form  noValidate autoComplete="off"onSubmit={onSubmit}>
            <div>
            <TextField 
            margin="normal"
            required
            fullWidth
            label="User" 
            name='user'
            onChange={onChangeHandler}
            variant="outlined" />
            
            </div>
            <div>
            <TextField
                margin="normal"
                required
                fullWidth
                name='password'
                variant="outlined"
                label="Password"
                type="password"
                autoComplete="current-password"
                onChange={onChangeHandler}
                />
            </div>
            <Button
            type="submit"
            fullWidth
            variant="contained"
            className={classes.submit}
            color="primary"
          >
            Sign In
          </Button>
        </form>
      </Container>
    </React.Fragment>
  );
}