import { URL } from './api';
import IPagination from '../interfaces/IPagination';

export type Params = {
  english: string;
  id: number | string;
}

export interface ResponseList {
  items: Params[];
  pagination: IPagination;
  api:any;
}

type ListParams = {
  api:any;
}

export default function list(props:ListParams):Promise<ResponseList | null>{
  const api:any = props.api;
    return new Promise( resolve => {
      const _url:string = `${URL.subskills.page}`;
      api.get(_url)
          .then(function (response:any) {
            resolve(response.data);
          })
          .catch(function (error:any) {console.log(error)
            if( error.response && error.response.data && error.response.data.error ){
               alert(error.response.data.error);
            } else {
                alert('Something wrong is going on, please try again later.');
            }
            resolve(null);
          });
    });
}

export type ResponseSection = {
  data: any;
  status: boolean;
}

export type ReadParams = {
  id:string|number;
  api:any;
}

export function read(props:ReadParams):Promise<ResponseSection | null>{
  const api:any = props.api;
  return new Promise( resolve => {
    const _url:string = `${URL.subskills.page}${props.id}`;
    api.get(_url)
        .then(function (response:any) {
          resolve(response.data);
        })
        .catch(function (error:any) {
          if( error.response && error.response.data && error.response.data.error ){
             alert(error.response.data.error);
          } else {
              alert('Something wrong is going on, please try again later.');
          }
          resolve(null);
        });
  });
}

export type SectionParams = {
  english?:string;
  spanish?:string;
  portuguese?:string;
  id?:number;
  skill_id?:number|string;
  api:any;
}

export function create(props:SectionParams):Promise<ResponseSection | null>{
  const api:any = props.api;
  return new Promise( resolve => {
    const _url:string = `${URL.subskills.page}`;
    api.post(_url, props)
        .then(function (response:any) {
          resolve(response.data);
        })
        .catch(function (error:any) {
          if( error.response && error.response.data && error.response.data.error ){
             alert(error.response.data.error);
          } else {
              alert('Something wrong is going on, please try again later.');
          }
          resolve(null);
        });
  });
}


