import list from '../../services/jobs';

async function JobsList(_page:number, _limit:number, setLoading:any, setPageData:any, api:any, search:string){
    setLoading(true);
    const data = await list({page:_page+1, limit:_limit, api, search});
    setLoading(false);

    if(data){
        setPageData(data);
    }
}

export default JobsList;