import React, { createContext, useState, useEffect, useContext } from 'react';
import * as auth from '../services/auth';
import api from '../services/api';

interface User{
    token: string;
}

interface AuthContextData {
    signed: boolean;
    user:User | null;
    signIn(args:auth.SignInParams):Promise<void>;
    signOut():void;
    loading:boolean;
    getTokenUserLogged():void;
    api:any;
}

const AuthContext = createContext<AuthContextData>({} as AuthContextData);

const TOKEN_APP_USER_JOBFISHING:string = 'TOKEN_APP_USER_JOBFISHING';

export const AuthProvider:React.FC = ({ children }) => {
    const [user, setUser] = useState<User | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(()=>{
        async function loadStorageData(){
            //checar se existe o cookie de login
            const tokernUserLogged = getTokenUserLogged();
            if(tokernUserLogged){
                api.defaults.headers['Authorization'] = `Bearer ${tokernUserLogged}`;      
                const response = await auth.loadByToken();
                userLogged(response);
            }
            setLoading(false);
        }
        loadStorageData();
    },[]);

    function getTokenUserLogged () { return localStorage.getItem(TOKEN_APP_USER_JOBFISHING) };

    async function signIn(args:auth.SignInParams){
        const response = await auth.signIn(args);
        userLogged(response);
    }

    function userLogged(response:auth.AuthResponse | null) {
        if (response) {
            setUser({token:response.token});
            api.defaults.headers['Authorization'] = `Bearer ${response.token}`;            
            window.localStorage.setItem(TOKEN_APP_USER_JOBFISHING, response.token);
        }
    }

    function signOut(){
        window.localStorage.removeItem(TOKEN_APP_USER_JOBFISHING);
        setUser(null);
    }

    return <AuthContext.Provider value={{ signed: !!user, signIn, getTokenUserLogged, user, signOut, loading, api }}>
        {children}
    </AuthContext.Provider>
}

export function useAuth() {
    const context = useContext(AuthContext);
    return context;
}