import { useEffect, useState, FormEvent } from "react";
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import { Button, TextField } from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import Search from '@material-ui/icons/Search';
import TablePagination from '@material-ui/core/TablePagination';
import CircularProgress from '@material-ui/core/CircularProgress';
import InputLabel from '@material-ui/core/InputLabel';
import {
    Link,
    useRouteMatch
} from "react-router-dom";
import { useAuth } from "../../contexts/auth";
import { ResponseList } from '../../services/app-users';
import HelpersList from './list-load';

import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';

interface FilterParams{
    name: string,
    state: string,
    city: string,
    zipcode: string,
    type:number|string;
}

const INITIAL_DATA:FilterParams = {
    name: '',
    state: '',
    city: '',
    zipcode: '',
    type: ''
};
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    }
  }),
);

const List = () => {
    const classes = useStyles();
    let { url } = useRouteMatch();
    const [isLoading, setLoading] = useState(true);
    const [pageData, setPageData] = useState<ResponseList | null>();
    const [page, setPage] = useState(0);
    const [limit, setlimit] = useState(10);
    const [searchString, setSearchString] = useState<string>('');
    const [filterData, setFilterData] = useState<FilterParams>(INITIAL_DATA);
    const {api} = useAuth();

    useEffect(()=>{
        async function load(){
            await HelpersList(page, limit, setLoading, setPageData, api, searchString);
        }
        load();
    },[page, limit, api, searchString]);

    const handleChangePage = async (event:any, newPage:any) => {
        setPage(newPage);
        await HelpersList(newPage, limit, setLoading, setPageData, api, searchString);
    };

    const handleChangeRowsPerPage = async (event:any) => {
        const _page:number = 1;
        const _limit:number = parseInt(event.target.value);
        setlimit(_limit);
        setPage(_page);
        await HelpersList(_page, _limit, setLoading, setPageData, api, searchString);
    };

    const filterChange = (event:any) => {
        const {name, value} = event.target;
        setFilterData({...filterData, ...{[name]: value}});
    };

    const onSubmit = async (event: FormEvent) => {
        event.preventDefault();
        const _f:any = filterData;
        let _s:any = new URLSearchParams(_f).toString();
        setSearchString(_s);
        setPageData(null);
        await HelpersList(page, limit, setLoading, setPageData, api, _s);
    }

    const SelectHandleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        const {value} = event.target;
        if (value){
            setFilterData({...filterData, ...{type: value as number }});
        }
    };

    function getPage(){
        return <>
                <form onSubmit={onSubmit}> 
                    <Grid container spacing={3} style={{marginBottom: 30}}>
                        <Grid item lg={2}>
                            <FormControl  className={classes.formControl}>
                            <InputLabel id="demo-simple-select-label">User type</InputLabel>
                                <Select
                                id="demo-simple-select"
                                value={filterData.type}
                                onChange={SelectHandleChange}
                                >
                                    <MenuItem value={''}>All users</MenuItem>
                                    <MenuItem value={1}>Helpers</MenuItem>
                                    <MenuItem value={2}>Owners</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        
                        <Grid item lg={3}>
                            <TextField value={filterData.name} onChange={filterChange}  name='name' fullWidth focused variant="outlined" label="Name" />
                        </Grid>
                        <Grid item lg={2}>
                            <TextField value={filterData.state} onChange={filterChange}  name='state' fullWidth focused variant="outlined" label="State" />
                        </Grid>
                        <Grid item lg={2}>
                            <TextField value={filterData.city} onChange={filterChange}  name='city' fullWidth focused variant="outlined" label="City" />
                        </Grid>
                        <Grid item lg={2}>
                            <TextField value={filterData.zipcode} onChange={filterChange}  name='zipcode' fullWidth focused variant="outlined" label="Zipcode" />
                        </Grid>
                        
                        <Grid item  xs={12} md={1} lg={1}>
                            <Button type="submit" variant="contained" color="primary" size="large">
                                <Search />
                            </Button>
                        </Grid>
                    </Grid>
                </form>
                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableHead>
                        <TableRow>
                            <TableCell>Name</TableCell>
                            <TableCell>Type</TableCell>
                        </TableRow>
                        </TableHead>
                        <TableBody>
                        {pageData ? pageData?.items.map((row) => (
                            <TableRow key={row.name}>
                            <TableCell component="th" scope="row">
                                <Link to={`${url}/${row.id}`}>
                                {row.name}
                                </Link>
                            </TableCell>
                            <TableCell component="th" scope="row">
                                <Link to={`${url}/${row.id}`}>
                                {row.helper ? 'Helper' : 'Owner'}
                                </Link>
                            </TableCell>
                            
                            </TableRow>
                        )) : null}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    component="div"
                    count={pageData ? (pageData.pagination.total || 0) : 0}
                    page={page}
                    onChangePage={handleChangePage}
                    rowsPerPage={limit}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </>;
    }

    return(
        <>
            {(!isLoading && pageData) ? getPage() : null}
            {isLoading ? <CircularProgress /> : null}
        </>
    );
}

export default List;