import { useEffect, useState } from "react";
import CircularProgress from '@material-ui/core/CircularProgress';
import Divider from '@material-ui/core/Divider';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useAuth } from "../../contexts/auth";
import {
  useParams
} from "react-router-dom";
import { read} from '../../services/companies';

interface FormProps {}

interface CompanyURL{
  id?:string | null | undefined;
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    box: {
      paddingTop: 20,
      paddingBottom: 20
    },
  }),
);

export const Form: React.FunctionComponent<FormProps> = (props:any) => {
  const company:CompanyURL = useParams();
  const [isLoading, setLoading] = useState(false);
  const [companyData, setCompanyData] = useState<any>(undefined);
  const {api} = useAuth();
  const classes = useStyles();


  useEffect(()=> {
    async function checkUser(){
      if(Object(company).hasOwnProperty('id')){
        const id = company.id;
        if(id){
          setLoading(true);
          const response = await read({id, api});

          if(response && response.data){
            setCompanyData(response.data);
          }
      
          setLoading(false);
        }
      }
    }
    
    checkUser();
  },[api, company]);

  function getPage(){

    if(!companyData) return null;

    return <>
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Company
        </Typography>
        {companyData.name} {`${companyData.phone_number ? ' - ' + companyData.phone_number: ''}`}
      </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Address
        </Typography>
        {`${companyData.state}, ${companyData.city}, ${companyData.street} - ${companyData.zipcode}`}
        </div>
      <Divider />
      <div className={classes.box}>
        <Typography variant="h5" gutterBottom>
          Subskills
        </Typography>
        {
          companyData.subskills ? companyData.subskills.map((item:{name:string, id:number}) => {
            return <div>{item.name}</div>
          }) : null
        }
      </div>
    </>;
  }

  return (
    <>
      {!isLoading ? getPage() : null}
      {isLoading ? <CircularProgress /> : null}
    </>
  );
};

export default Form;