import { FormEvent, useEffect, useState } from "react";
import { TextField, Button, makeStyles, createStyles, Theme } from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  useParams,
  useHistory
} from "react-router-dom";
import { read, UserParams, create, update, destroy } from '../../services/users';
import { useAuth } from "../../contexts/auth";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonWrapper: {
      marginTop: "8px",
      '& > *': {
        margin: theme.spacing(1),
      },
    },
  })
);


interface FormProps {}

interface UserURL{
  id?:string | null | undefined;
}

const INITIAL_DATA:UserParams = {
  name: '',
  user: '',
  password: '',
  api: undefined,
  id: undefined
};

export const Form: React.FunctionComponent<FormProps> = (props:any) => {
  const classes = useStyles();
  const user:UserURL = useParams();
  const [formData, setFormData] = useState(INITIAL_DATA);
  const [isLoading, setLoading] = useState(false);
  const {api} = useAuth();
  const history = useHistory();

  const handleChange = (event:any) => {
    const {name, value} = event.target;
    setFormData({...formData, ...{[name]: value}});
  };
  
  useEffect(()=> {
    function checkUser(){
      if(Object(user).hasOwnProperty('id')){
        const id = user.id;
        if(id) loadData(id);
      }
    }

    async function loadData(id:string) {
      setLoading(true);
      const response = await read({id, api});
      if(response && response.data){
        setFormData(response.data);
      }
  
      setLoading(false);
    }
    
    checkUser();
  },[user, api]);

  const onSubmit = async (event: FormEvent) => {
    event.preventDefault();
    let response:any = null;

    if (!formData.name || !formData.user || !formData.password) {
      alert('All fields are required');
      return;
    }

    setLoading(true);

    formData.api = api;

    try {
      let message:string = '';
      if (formData && formData.id) {
        response = await update(formData);
        message = response.message || 'Item updated!';
      } else {
        response = await create(formData);
        formData.id = response.item.id;
        message = response.message || 'Item created!';
      }

      setLoading(false);
      setFormData(formData);
      alert(message);
    } catch (error) {
      alert('Something went wrong, please try again later!');
    }
  };

  async function removeHandler(){
    let response:any = null;

    setLoading(true);

    if (!formData.id) return;

    try {
      let message:string = '';
      response = await destroy({id: formData.id, api});
      message = response.message || 'Item removed!';

      setLoading(false);
      setFormData(formData);
      alert(message);
      history.push("/users");
    } catch (error) {
      alert('Something went wrong, please try again later!');
    }
  }

  function getPage() {
    return <form onSubmit={onSubmit}>      
    <Grid item xs={6}>
        <TextField style={{ marginBottom: 20 }} fullWidth label="Name" name='name' value={formData.name} onChange={handleChange} InputLabelProps={{
        shrink: true,
      }} />
    </Grid>
    <Grid item xs={6}>
        <TextField style={{ marginBottom: 20 }}  fullWidth label="User" name='user' value={formData.user} onChange={handleChange} InputLabelProps={{
        shrink: true,
      }} />
    </Grid>
    <Grid item xs={6}>
        <TextField fullWidth label="Password" name='password'  value={formData.password} onChange={handleChange} type="password" InputLabelProps={{
        shrink: true,
      }}  />
    </Grid>
  
    <div className={classes.buttonWrapper}>
        <Button type="submit" variant="contained" color="primary">
            Save
        </Button>
        {
          formData.id && <Button type='button' variant="contained" color="secondary" onClick={removeHandler}>
                              Remove
                          </Button>
        }
    </div>
</form>;
  }

  return (
    <>
      {!isLoading ? getPage() : null}
      {isLoading ? <CircularProgress /> : null}
    </>
  );
};

export default Form;