import list from '../../services/users';

async function UserList(_page:number, _limit:number, setLoading:any, setPageData:any, api:any){
    setLoading(true);
    const data = await list({page:_page+1, limit:_limit, api});
    setLoading(false);
    if(data){
        setPageData(data);
    }
}

export default UserList;