import { FormEvent, useEffect, useState } from "react";
import { TextField, Button, makeStyles, createStyles, Theme  } from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  useParams,
  useHistory
} from "react-router-dom";
import { read, SectionParams, create, update, destroy } from '../../services/skills';
import { useAuth } from "../../contexts/auth";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonWrapper: {
      marginTop: "8px",
      '& > *': {
        margin: theme.spacing(1),
      },
    },
    formControl: {
      minWidth: 220,
    },
  })
);

interface FormProps {}

interface UserURL{
  id?:string | null | undefined;
}

const INITIAL_DATA:SectionParams = {
  english:'',
  portuguese: '',
  spanish: '',
  api: undefined
}

export const Form: React.FunctionComponent<FormProps> = (props:any) => {
  const classes = useStyles();
  const user:UserURL = useParams();
  const [formData, setFormData] = useState(INITIAL_DATA);
  const [isLoading, setLoading] = useState(false);
  const { api } = useAuth();
  const history = useHistory();

  const handleChange = (event:any) => {
    const {name, value} = event.target;
    setFormData({...formData, ...{[name]: value}});
  };
  
  useEffect(()=> {
    async function checkUser(){
      setLoading(true);

      if(Object(user).hasOwnProperty('id')){
        const id = user.id;
        if(id){
          const response = await read({id, api});

          if(response && response.data){
            setFormData(response.data);
          }
      
          setLoading(false);
        }
      } else {
        setLoading(false);
      }
    }
    
    checkUser();
  },[user, api]);

  const onSubmit = async (event: FormEvent) => {
    event.preventDefault();
    let response:any = null;

    if (!formData.english || !formData.portuguese || !formData.spanish) {
      alert('All fields are required');
      return;
    }

    setLoading(true);

    formData.api = api;

    try {
      let message:string = '';
      if (formData && formData.id) {
        response = await update(formData);
        message = response.message || 'Item updated!';
      } else {
        response = await create(formData);
        formData.id = response.item.id;
        message = response.message || 'Item created!';
      }

      setLoading(false);
      setFormData(formData);
      alert(message);
    } catch (error) {
      alert('Something went wrong, please try again later!');
    }
  };


  async function removeHandler(){
    let response:any = null;

    setLoading(true);

    if (!formData.id) return;

    try {
      let message:string = '';
      response = await destroy({id: formData.id, api});
      message = response.message || 'Item removed!';

      setLoading(false);
      setFormData(formData);
      alert(message);
      history.push("/categories");
    } catch (error) {
      alert('Something went wrong, please try again later!');
    }
  }

  const SelectHandleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    const {value} = event.target;
    if (value){
      setFormData({...formData, ...{skill_id: value as number }});
    }
  };

  function getPage() {
    return <form onSubmit={onSubmit}>
    <Grid item xs={6}>
        <TextField style={{ marginBottom: 20 }} fullWidth label="English" name='english' value={formData.english} onChange={handleChange} InputLabelProps={{
        shrink: true,
      }} />
    </Grid>
    <Grid item xs={6}>
        <TextField style={{ marginBottom: 20 }}  fullWidth label="Spanish" name='spanish' value={formData.spanish} onChange={handleChange} InputLabelProps={{
        shrink: true,
      }} />
    </Grid>
    <Grid item xs={6}>
        <TextField fullWidth label="Portuguese" name='portuguese'  value={formData.portuguese} onChange={handleChange} InputLabelProps={{
        shrink: true,
      }}  />
    </Grid>
  
    <div className={classes.buttonWrapper}>
        <Button type="submit" variant="contained" color="primary">
            Save
        </Button>
        {
          formData.id && <Button type='button' variant="contained" color="secondary" onClick={removeHandler}>
                              Remove
                          </Button>
        }
    </div>
</form>;
  }

  return (
    <>
      {!isLoading ? getPage() : null}
      {isLoading ? <CircularProgress /> : null}
    </>
  );
};

export default Form;